<template>
  <div class="table">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-tickets"></i> Comment信息
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          size="mini"
          class="handle-del mr10"
          @click="delAll"
          >Batch Delete</el-button
        >
        <el-input
          v-model="select_word"
          size="mini"
          placeholder="Select keywords"
          class="handle-input mr10"
        ></el-input>
      </div>
      <el-table
        :data="tableData"
        size="mini"
        border
        style="width: 100%"
        ref="multipleTable"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="name" label="用户"></el-table-column>
        <el-table-column
          prop="content"
          label="CommentContent"
        ></el-table-column>
        <el-table-column label="Operation" width="150">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >Modify</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row.id)"
              >Delete</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- Modify弹出框 -->
    <el-dialog title="Modify" :visible.sync="editVisible" width="600px">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="CommentContent" size="mini">
          <el-input v-model="form.content"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="editVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="saveEdit">OK</el-button>
      </span>
    </el-dialog>

    <!-- 删除提示框 -->
    <el-dialog title="Hint" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt" align="center">
        Deletion cannot be recovered. Are you sure to delete it?
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="delVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="deleteRow">OK</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { HttpManager } from "../api/index";

export default {
  name: "comment-page",
  mixins: [mixin],
  data() {
    return {
      tableData: [],
      tempDate: [],
      multipleSelection: [],
      editVisible: false,
      delVisible: false,
      select_word: "",
      form: {
        id: "",
        userId: "",
        songId: "",
        songListId: "",
        content: "",
        type: "",
        up: ""
      },
      idx: -1
    };
  },
  watch: {
    select_word: function() {
      if (this.select_word === "") {
        this.tableData = this.tempDate;
      } else {
        this.tableData = [];
        for (let item of this.tempDate) {
          if (item.name.includes(this.select_word)) {
            this.tableData.push(item);
          }
        }
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    // 获取Comment
    getData() {
      this.tableData = [];
      this.tempDate = [];
      let promise;
      if (this.$route.query.type === 0) {
        promise = HttpManager.getCommentOfSongId(this.$route.query.id);
      } else if (this.$route.query.type === 1) {
        promise = HttpManager.getCommentOfSongListId(this.$route.query.id);
      }
      promise.then(res => {
        for (let item of res) {
          this.getUsers(item.userId, item);
        }
      });
    },
    getUsers(id, item) {
      HttpManager.getUserOfId(id)
        .then(res => {
          let o = item;
          o.name = res[0].username;
          this.tableData.push(o);
          this.tempDate.push(o);
        })
        .catch(err => {
          console.log(err);
        });
    },
    // Modify
    handleEdit(row) {
      this.idx = row.id;
      this.form = {
        id: row.id,
        userId: row.userId,
        songId: row.songId,
        songListId: row.songListId,
        content: row.content,
        type: row.type,
        up: row.up
      };
      this.editVisible = true;
    },
    // 保存Modify
    saveEdit() {
      let params = new URLSearchParams();
      params.append("id", this.form.id);
      params.append("userId", this.form.userId);
      params.append(
        "songId",
        this.form.songId === null ? "" : this.form.songId
      );
      params.append(
        "songListId",
        this.form.songId === null ? "" : this.form.songListId
      );
      params.append("content", this.form.content);
      params.append("type", this.form.type);
      params.append("up", this.form.up);
      HttpManager.updateCommentMsg(params)
        .then(res => {
          if (res.code === 1) {
            this.getData();
            this.notify("Modified successfully", "success");
          } else {
            this.notify("Failed to modify", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.editVisible = false;
    },
    // 确定删除
    deleteRow() {
      HttpManager.deleteComment(this.idx)
        .then(res => {
          if (res) {
            this.getData();
            this.notify("Deleted successfully", "success");
          } else {
            this.notify("Failed to delete", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.delVisible = false;
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
</style>



